@media (max-width: 725px) {
    .box1 {
        flex-direction: column-reverse;
        align-items: center;
    }
    .box2 {
        flex-direction: column;
        align-items: center;
    }
    .box3 {
        flex-direction: column-reverse;
        align-items: center;
    }
    .box4 {
        flex-direction: column;
        align-items: center;
    }
    .pages {
        text-align: center;
    }
    .pages h2 {
        text-align: center;
    }
    .pages p {
        text-align: center;
    }
    .pages a {
        text-align: center;
    }
}